	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" title=\":triangular_flag_on_post:\" alt=\":triangular_flag_on_post:\" class=\"forum-image\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" title=\":busts_in_silhouette:\" alt=\":busts_in_silhouette:\" class=\"forum-image\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" title=\":pencil:\" alt=\":pencil:\" class=\"forum-image\"></p>\n<h3><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/checkered_flag.png?v=3\" title=\":checkered_flag:\" alt=\":checkered_flag:\" class=\"forum-image\"> Problem Explanation:</h3>\n<p>Convert the given string to a lowercase sentence with words joined by dashes.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String' target='_blank' rel='nofollow'>String global object</a></li>\n<li><a>JS Regex Resources</a></li>\n<li><a href='http://forum.freecodecamp.com/t/javascript-string-prototype-replace/15942' target='_blank' rel='nofollow'>JS String Prototype Replace</a></li>\n<li><a href='http://forum.freecodecamp.com/t/javascript-string-prototype-tolowercase/15948' target='_blank' rel='nofollow'>JS String Prototype ToLowerCase</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 1</h2>\n<p>Create a regular expression for all white spaces and underscores.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 2</h2>\n<p>You will also have to make everything lowercase.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" title=\":speech_balloon:\" alt=\":speech_balloon:\" class=\"forum-image\"> Hint: 3</h2>\n<p>The tricky part is getting the regular expression part to work, once you do that then just turn the uppercase to lowercase and replace spaces with underscores using <code>replace()</code>.</p>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"warning sign\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" title=\":beginner:\" alt=\":beginner:\" class=\"forum-image\"> Basic Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function spinalCase(str) {\n  // Create a variable for the white space and underscores.\n  var regex = /\\s+|_+/g;\n\n  // Replace low-upper case to low-space-uppercase\n  str = str.replace(/(<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>a-z])([A-Z])/g, '$1 $2');\n\n  // Replace space and underscore with -\n  return str.replace(regex, '-').toLowerCase();\n}\n\n// test here\nspinalCase('This Is Spinal Tap');</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLnS/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li><strong>regex</strong> contains the regular expression <code>/\\s+|_+/g</code>, which will select all white spaces and underscores.</li>\n<li>The first <code>replace()</code> puts a space before any encountered uppercase characters in the string <strong>str</strong> so that the spaces can be replaced by dashes later on.</li>\n<li>While returning the string, another <code>replace()</code> replaces spaces and underscores with dashes using <strong>regex</strong>.</li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/sunflower.png?v=3\" title=\":sunflower:\" alt=\":sunflower:\" class=\"forum-image\"> Intermediate Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function spinalCase(str) {\n  // Replace low-upper case to low-space-uppercase\n  str = str.replace(/(<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>a-z])([A-Z])/g, '$1 $2');\n  // Split on whitespace and underscores and join with dash\n  return str.toLowerCase().split(/(?:_| )+/) .join('-');\n}\n\n// test here\nspinalCase('This Is Spinal Tap');</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/CLnT/0' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li>Similar to the first solution, the first <code>replace()</code> puts a space before any encountered uppercase characters in the string <strong>str</strong> so that the spaces can be replaced by dashes later on.</li>\n<li>Instead of using <code>replace()</code> here to replace whitespace and underscores with dashes, the string is <code>split()</code> on the regular expression <code>/(?:_| )+/</code> and <code>join()</code>-ed on <code>-</code>.</li>\n</ul>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href='http://forum.freecodecamp.com/t/javascript-string-prototype-split/15944' target='_blank' rel='nofollow'>JS String Prototype Split</a></li>\n<li><a href='http://forum.freecodecamp.com/t/javascript-array-prototype-join/14292' target='_blank' rel='nofollow'>JS Array Prototype Join</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rotating_light.png?v=3\" title=\":rotating_light:\" alt=\":rotating_light:\" class=\"forum-image\"> Advanced Code Solution:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>function spinalCase(str) {\n  // \"It's such a fine line between stupid, and clever.\"\n  // --David St. Hubbins\n\n  return str.split(/\\s|_|(?=<a href='https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3 \":rocket:\"' target='_blank' rel='nofollow'>A-Z])/).join('-').toLowerCase()\n}</code></pre>\n      </div>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" title=\":rocket:\" alt=\":rocket:\" class=\"forum-image\"> <a href='https://repl.it/EUZV' target='_blank' rel='nofollow'>Run Code</a></p>\n<h3>Code Explanation:</h3>\n<ul>\n<li>\n<p>Split the string at one of the following conditions (<em>converted to an array</em>)</p>\n<ul>\n<li>a whitespace character <a href='http://devdocs.io/javascript/global_objects/string/split' target='_blank' rel='nofollow'><code>\\s</code>] is encountered</li>\n<li>underscore character [<code>_</code>] is encountered</li>\n<li>or is followed by an uppercase letter [<code>(?=[A-Z])</code>]</li>\n</ul>\n</li>\n<li>Join the array using a hyphen (<code>-</code>)</li>\n<li>Lowercase the whole resulting string</li>\n</ul>\n<h4>Relevant Links</h4>\n<ul>\n<li>[String#split</a></li>\n<li><a href='http://devdocs.io/javascript/global_objects/regexp' target='_blank' rel='nofollow'>RegExp</a></li>\n<li><a href='http://devdocs.io/javascript/global_objects/array/join' target='_blank' rel='nofollow'>Arrray#join</a></li>\n<li><a href='http://devdocs.io/javascript/global_objects/string/tolowercase' target='_blank' rel='nofollow'>String#toLowerCase</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" title=\":clipboard:\" alt=\":clipboard:\" class=\"forum-image\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories â€” <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" title=\":traffic_light:\" alt=\":traffic_light:\" class=\"forum-image\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" title=\":warning:\" alt=\":warning:\" class=\"forum-image\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" title=\":point_right:\" alt=\":point_right:\" class=\"forum-image\"> <a href='http://forum.freecodecamp.com/t/algorithm-article-template/14272' target='_blank' rel='nofollow'><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>","fields":{"slug":"/certificates/spinal-tap-case/"},"frontmatter":{"title":"Spinal Tap Case"}}},"pathContext":{"slug":"/certificates/spinal-tap-case/"}}

